<style>
  @import '~fullcalendar/dist/fullcalendar.css';
</style>

<template>
  <div class="event-calendar">
    <full-calendar :config="config" :events="events"/>
  </div>
</template>

<script>
  import moment from 'moment';

  export default {
    name: "EventCalendar",
    data () {
			return {
				events: [
					{
						title: 'test',
						allDay: true,
						start: moment(),
						end: moment().add(9, 'd'),
					}
				],
				config: {
					defaultView: 'month',
					eventRender: function(event, element) {
						
					}
				},
			}
  	}
  }
</script>



