<style scoped>
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }
  .layout-header-bar{
    background: #fff;
    box-shadow: 0 1px 1px rgba(0,0,0,.1);
  }
  .layout-logo-left{
    width: 90%;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    margin: 15px auto;
  }
  .menu-icon{
    transition: all .3s;
  }
  .rotate-icon{
    transform: rotate(-90deg);
  }
  .menu-item span{
    display: inline-block;
    overflow: hidden;
    width: 69px;
    text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: bottom;
    transition: width .2s ease .2s;
  }
  .menu-item i{
    transform: translateX(0px);
    transition: font-size .2s ease, transform .2s ease;
    vertical-align: middle;
    font-size: 16px;
  }
  .collapsed-menu span{
    width: 0px;
    transition: width .2s ease;
  }
  .collapsed-menu i{
    transform: translateX(5px);
    transition: font-size .2s ease .2s, transform .2s ease .2s;
    vertical-align: middle;
    font-size: 22px;
  }
  .main-content{
    padding: 20px;
  }
</style>

<template>
    <div class="layout">
      <Layout>
        <Sider ref="side1" hide-trigger collapsible :collapsed-width="78" v-model="isCollapsed">
          <Menu active-name="1-2" theme="dark" width="auto" :class="menuitemClasses">
            <MenuItem name="1-1">
              <Icon type="calendar"></Icon>
              <span>Calendar</span>
            </MenuItem>
          </Menu>
        </Sider>
        <Layout>
          <Header :style="{padding: 0}" class="layout-header-bar">
            <Icon @click.native="collapsedSider" :class="rotateIcon" :style="{margin: '20px 20px 0', float: 'left'}" type="navicon-round" size="24"></Icon>
          </Header>
          <Content :style="{margin: '20px', background: '#fff', minHeight: '260px'}">
            <div class="main-content">
              <Row :gutter="10">
                <Col span="10">
                  <div class="setup-event__main">
                    <SetupEvent @onSubmit="submitEvent"></SetupEvent>
                  </div>
                </Col>
                <Col span="14">
                  <div class="event-calendar__main">
                    <!--<EventCalendar></EventCalendar>-->
                    <July :events="events"></July>
                  </div>
                </Col>
              </Row>
            </div>
          </Content>
        </Layout>
      </Layout>
    </div>
</template>

<script>
  import SetupEvent from '@/components/SetupEvent'
  import EventCalendar from '@/components/EventCalendar'
  import July from '@/components/July'
  import Api from '@/api/index';

  export default {
    data () {
      return {
        isCollapsed: true
      }
    },
    components: {
      SetupEvent,
      EventCalendar,
      July
    },
    mounted() {
      this.$store.dispatch('initializeAllEvents').then(res => {
        for(let i = 0; i < this.events.length; i++){
          // console.log(this.events[i].exclude_dates)
          console.log(this.decodeExclude(this.events[i].exclude_dates))
        }
      })
    },
    computed: {
      rotateIcon () {
        return [
          'menu-icon',
          this.isCollapsed ? 'rotate-icon' : ''
        ];
      },
      menuitemClasses () {
        return [
          'menu-item',
          this.isCollapsed ? 'collapsed-menu' : ''
        ]
      },
      events: {
        get() {
          return this.$store.state.allEvents
        }
      }
    },
    methods: {
      collapsedSider () {
        this.$refs.side1.toggleCollapse();
      },
      decodeExclude(val){
        let excludeDates = []

        let fact2 = 1
        for(let i=1; i < 8; i++){
          if(fact2 & val){
            excludeDates.push(i)
          }
          fact2 += fact2
        }

        return excludeDates
      },
      submitEvent(val){
        // console.log(val)
        this.$store.dispatch('postEvent', val)
      }
    }
  }
</script>



